.contact {
    width: 100%;
    max-width: $max-width-desktop;
    display: block;
    margin-bottom: 2rem;
    padding: 0 1rem;

    @media (max-width: $min-width-desktop) {
      max-width: 100%;
      padding: 0 1rem;
    }
  
    &__description {
      font-size: .9rem;
      font-weight: 400;
      margin-bottom: 2rem;
    }
  
    &__email {
      color: $blue;
      font-weight: 600;
      text-decoration: none;
      font-size: 1.1rem;
      margin-bottom: 2rem;
    }
  
    &__links {
      list-style: none;
      padding: 0;
  
      &>li {
        display: inline-block;
        padding-right: .5rem;
      }
    }
  
    &__title {
      font-weight: 600;
      font-size: 2rem;
  
      &--lightblue::before {
        content: url("../assets/img/decoline-lightblue.svg");
        display: block;
      }
    }
  }
  
  .links {
    &__item {
        max-width: 1.5rem;
        max-height: 1.5rem;
  
        &--ico {
          width: 100%;
          height: 100%;
        }
  
      &--svg {
        fill: $white;
      }
    }
  }
  
  .footer {
    background: $darkblue;
    width: 100%;
    padding: 0;
    color: $white;
    display: flex;
    flex-direction: column;
    align-items: center;
  }